<template>
  <div id="register">
    <div class="page-title">{{ regstration }}</div>
    <div class="login-form">
    	<form action="#" method="post" autocomplete="off">
    		<div class="form-line">
    			<input type="email" name="email" :placeholder="email">
    		</div>
    		<div class="form-line">
    			<input type="password" name="password" :placeholder="password">
    		</div>
    		<div class="form-line">
    			<button type="button">{{ sign_in }}</button>
    		</div>
    		<div class="form-social-links">
    			<button type="button" class="facebook-login">{{ log_in }}</button>
    			<button type="button" class="google-login">{{ log_in }}</button>
    		</div>
    	</form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'register',
  data () {
    return {
      pageTitle : '',
      email: '.',
      password: '',
      log_in: '',
      regstration: '',
      sign_in: '',
    }
  },
  created : function () {
  	if (window.ta_user_id > 0) {
  	  this.$router.push('/profile');
  	}
  }
}
</script>

<style lang="sass">
#register
	background: url('./assets/login-page-bg.jpg') center center no-repeat
	background-size: cover
	min-height: 100%

	form
		position: absolute
		top: 50%
		margin-top: -90px
		width: 100%

	.form-line
		height: 60px
		line-height: 60px
		width: 90%
		margin: 0 auto

		input
			width: 100%
			height: 40px
			border: 0
			outline: 0
			background: #fff
			padding: 5px 15px
			font-size: 16px
			border-bottom: 1px #fd5a68 solid

		button
			width: 100%
			height: 40px
			background: #fd5a68
			color: #fff
			text-transform: uppercase
			border: 0
			outline: 0
			font-size: 14px

	.form-social-links
		text-align: center
		line-height: 60px
		width: 60%
		margin: 15px auto

		button
			display: block
			border: 0
			outline: 0
			margin: 15px 0
			width: 100%
			height: 30px
			cont-size: 14px
			color: #fff
			text-align: center 
			position: relative


		.facebook-login
			background: #3c66c4
			line-height: 20px

		.facebook-login:after
			content: ''
			position: absolute
			right: 20px
			top: 5px
			margin-right: 20px
			width: 20px
			height: 20px
			background: url('./assets/facebook-icon.png') center center no-repeat

		.google-login
			background: #cf4332

		.google-login:after
			content: ''
			position: absolute
			right: 20px
			top: 5px
			margin-right: 20px
			width: 20px
			height: 20px
			background: url('./assets/google-icon.png') center center no-repeat


	.page-title 
		position: absolute
		top: 50px
		left: 50%
		width: 260px
		height: 150px
		line-height: 150px
		margin-left: -130px
		text-align: center
		font-size: 25px
		color: #fff
		text-transform: uppercase
		font-family: 'Romul'
		background: url('./assets/register-page-title.png') center center no-repeat



</style>



