<template>
  <div id="profile">
    profile page

    <span @click="logout">log out</span>
  </div>
</template>

<script>
import Vue from 'vue'
import { EventBus } from '../event-bus.js';

export default {
  name: 'profile',
  data () {
    return {
      
    }
  },
  methods : {
  	logout () {
  	  window.ta_user_id = 0;
  	  localStorage.setItem('ta_user_id', 0);
  	  EventBus.$emit('user-exited');

  	  this.$router.push('/');
  	},
  },
  created : function () {
  	if (window.ta_user_id==0) {
  	  this.$router.push('/login');
  	}
  }
}
</script>

<style lang="sass">

</style>



