<template>
  <div id="organizerGuests">
    <div class="page-header">
      	<a href="javascript:history.back();" v-if="goBack" class="back-link"></a>
		<div>{{ title }}</div>
	</div>

	<div class="taskStats">
		{{ you_have_in_list }}
		{{ guests_ }} <span>{{ totalGuests }}</span>,
		{{ of_which }} <span>{{ in_processGuests }}</span> {{ in_processLbl }},
		<span>{{ doneGuests }}</span> {{ doneLbl }}.
	</div>

	<div class="taskStatsGraphs">
		<div class="graph newTasks">
			<div class="graph-content" :style="'width:' + newGuestsWidth + '%;'">{{ newGuestsWidth }}%</div>
		</div>
		<div class="graph in_processTasks">
			<div class="graph-content" :style="'width:' + in_processGuestsWidth + '%;'">{{ in_processGuestsWidth }}%</div>
		</div>
		<div class="graph doneTasks">
			<div class="graph-content" :style="'width:' + doneGuestsWidth + '%;'">{{ doneGuestsWidth }}%</div>
		</div>
	</div>

	<div class="footer-block">
		<p>{{ guests_list }}</p>
    <div class="groups-line">
  		<div class="task-gorups" :style="'width:' + ((organizer.guests.length + 1) * 110) + 'px'">
  			<div v-for="group in organizer.guests" class="task-group">
  				<span @click="group.opened=true">{{ group.name }}</span>
  			</div>
  			<div class="task-group add-task-group" @click="showGroupForm=true">
  				{{ add }}
  			</div>
  		</div>
	   </div>
	</div>

	<div v-for="(group, index) in organizer.guests" v-if="group.opened">
		<div class="popup">
			<div class="popup-content">
				<div class="close-popup" @click="group.opened=false"></div>
				<div class="popup-title">{{ group.name }}</div>

				<div v-if="group.guests.length > 0">
					<div v-for="(task, guestIndex) in group.guests" @click="openEditItem(index, guestIndex)" class="task-item">
						<v-touch @pan="pan" @panend="panend" @swiperight="changeStatus(index, guestIndex)" @swipeleft="deleteGuest(index, guestIndex)" :swipe-options="{ threshold: 100 }" :class="'panner panstatus-' + task.status">
							<div class="panStatuses">
								<div class="panstatus-new">{{ newGuest }}</div>
								<div class="panstatus-process">{{ in_processGuest }}</div>
								<div class="panstatus-done">{{ doneGuest }}</div>
							</div>
							{{ task.name }}
							<div class="pandelete">{{ deleteLbl }}</div>
						</v-touch>
					</div>
				</div>

				<div v-else class="list-empty">{{ no_guests }}</div>

				<div class="popup-footer">
					<div class="col col-33"><span @click="deleteGroup(index)" class="deleteItem"></span></div>
					<div class="col col-33"><span @click="showTheItemForm(index)" class="addItem"></span></div>
					<div class="col col-33"><span @click="group.opened=false" class="closeItem"></span></div>
				</div>

			</div>
		</div>
	</div>

	<div v-if="showGroupForm" class="popup">
		<div class="popup-content">
			<div class="close-popup" @click="showGroupForm=false"></div>
			<div class="popup-title">{{ enter_list_name }}</div>
			<div class="add-group">
				<input type="text" id="new-group" :class="new_group_error ? 'has-error' : ''">
				<button type="button" @click="addgroup" style="top:0">+</button>
			</div>
		</div>
	</div>

	<div v-if="showItemForm" class="popup">
		<div class="popup-content">
			<div class="close-popup" @click="showItemForm=false"></div>
			<div class="popup-title">{{ enter_item_info }}</div>
			<div class="add-group">
        <div>
          <input type="text" id="new-item" :placeholder="enter_item_name" :class="new_item_error ? 'has-error' : ''">
        </div>
        <div>
          <input type="number" id="new-item-count" value="1" :placeholder="enter_item_count" :class="new_item_count_error ? 'has-error' : ''">
        </div>
				<button type="button" @click="addItem">+</button>
			</div>
		</div>
	</div>

  <div v-if="showItemEditForm" class="popup">
		<div class="popup-content">
			<div class="close-popup" @click="showItemEditForm=false"></div>
			<div class="popup-title">{{ edit_item_info }}</div>
			<div class="add-group">
        <div>
          <input type="text" id="new-item-edit" :value="currentGuest.name" :placeholder="enter_item_name" :class="new_item_error ? 'has-error' : ''">
        </div>
        <div>
          <input type="number" id="new-item-count-edit" :value="currentGuest.count" :placeholder="enter_item_count" :class="new_item_count_error ? 'has-error' : ''">
        </div>
				<button type="button" @click="changeItem()">OK</button>
			</div>
		</div>
	</div>

  </div>
</template>

<script>
import Vue from 'vue'
var VueTouch = require('vue-touch')
Vue.use(VueTouch, {name: 'v-touch'})

export default {
  name: 'organizerGuests',
  data () {
    return {
    	title : '',

      goBack : goBack(),

    	// main data storage of the component
    	organizer : ta_organizer,
      currentGuest : {},

      currentGroupIndex : 0,
      currentItemIndex : 0,

    	guests_list : ' ',
    	add : '',
    	deleteLbl : '',
    	closeLbl : '',
    	no_guest : '  ',

    	enter_list_name : '  ',
      edit_item_info : ' ',
    	enter_item_info : '   ',
    	enter_item_name : ' \' ',
    	enter_item_count : '  ',

    	newGuest : ' ',
    	in_processGuest : '',
    	doneGuest : '',

    	you_have_in_list : '   ',
    	guests_ : '',
    	of_which : ' ',
    	in_processLbl : ' ',
    	doneLbl : '',

    	totalGuests : ta_organizer.guestsTotal,
    	newGuests : 0,
    	in_processGuests : 0,
    	doneGuests : 0,

    	showGroupForm : false,
    	showItemForm : false,
    	showItemEditForm : false,

    	new_group_error : false,
    	new_item_error : false,
    	new_item_count_error : false,

    	currentGroupIndex : null,

    }
  },
  computed : {
  	newGuestsWidth () {
  		return (parseInt(this.totalGuests) > 0) ? parseInt((this.newGuests / this.totalGuests) * 100) : 0
  	},
  	in_processGuestsWidth () {
  		return (parseInt(this.totalGuests) > 0) ? parseInt((this.in_processGuests / this.totalGuests) * 100) : 0
  	},
  	doneGuestsWidth () {
  		return (parseInt(this.totalGuests) > 0) ? parseInt((this.doneGuests / this.totalGuests) * 100) : 0
  	},
  },
  methods : {
  	addgroup () {
  		this.new_group_error = false

  		// chekc new name entered
  		var name = document.getElementById('new-group').value;
  		if (name.length==0) {
  			this.new_group_error = true
  			return
  		}

  		// create new group
  		var newGuest = {
  			'name' : name,
  			'guests' : [],
  			'opened' : false,
  		}

  		// save new group
  		ta_organizer.guests.push(newGuest)
  		localStorage.setItem('ta_organizer', JSON.stringify(ta_organizer))

  		// close add group popup
  		this.showGroupForm = false
  	},

  	deleteGroup (index) {
  		// delete group and save
  		ta_organizer.guests.splice(index, 1)
  		localStorage.setItem('ta_organizer', JSON.stringify(ta_organizer))

  		this.recountGuests()
  	},


  	showTheItemForm (index) {
  		this.showItemForm = true
  		this.currentGroupIndex = index
  	},


  	addItem () {
  		this.new_item_error = false
  		this.new_item_count_error = false

  		// chekc new name entered
  		var name = document.getElementById('new-item').value;
  		var count = document.getElementById('new-item-count').value;
  		if (name.length==0) {
  			this.new_item_error = true
  			return
  		}

      if (parseInt(count)==0) {
  			this.new_item_count_error = true
  			return
  		}

  		// create new group
  		var newGuest = {
  			'name' : name,
  			'count' : count,
  			'status' : '0',
  		}

  		// save new group
  		ta_organizer.guests[this.currentGroupIndex].guests.push(newGuest)
  		localStorage.setItem('ta_organizer', JSON.stringify(ta_organizer))

  		// close add group popup
  		this.showItemForm = false

  		this.recountGuests()
  	},


    openEditItem (index, guestIndex) {
      this.currentGroupIndex = index
      this.currentItemIndex = guestIndex

      this.currentGuest = ta_organizer.guests[index].guests[guestIndex]
      this.showItemEditForm = true
    },


    changeItem() {
      this.new_item_error = false
  		this.new_item_count_error = false

  		// chekc new name entered
  		var name = document.getElementById('new-item-edit').value;
  		var count = document.getElementById('new-item-count-edit').value;
  		if (name.length==0) {
  			this.new_item_error = true
  			return
  		}

      if (parseInt(count)==0 || count=='') {
  			this.new_item_count_error = true
  			return
  		}

      // save
  		ta_organizer.guests[this.currentGroupIndex].guests[this.currentItemIndex].name = name
  		ta_organizer.guests[this.currentGroupIndex].guests[this.currentItemIndex].count = count

  		localStorage.setItem('ta_organizer', JSON.stringify(ta_organizer))

  		// close add group popup
  		this.showItemEditForm = false

  		this.recountGuests()
    },


  	closeGroups () {
  		for (var i in ta_organizer.guests) {
  			ta_organizer.guests[i].opened = false
  		}
  	},

  	recountGuests () {
  		var newGuestsCounter = 0

  		for (var i in ta_organizer.guests) {
        for (var index in ta_organizer.guests[i].guests) {
          newGuestsCounter+= parseInt(ta_organizer.guests[i].guests[index].count)
        }
  		}

    	this.totalGuests = newGuestsCounter

  		ta_organizer.guestsTotal = newGuestsCounter
  		localStorage.setItem('ta_organizer', JSON.stringify(ta_organizer))

      console.log(ta_organizer);

  		this.recountStats()
  	},

  	recountStats () {
    	var newGuests = 0
    	var in_processGuests = 0
    	var doneGuests = 0

    	for (var i in ta_organizer.guests) {
    		for (var index in ta_organizer.guests[i].guests) {
    			if (parseInt(ta_organizer.guests[i].guests[index].status) == 0) {
    				newGuests+= parseInt(ta_organizer.guests[i].guests[index].count)
    			}

    			if (parseInt(ta_organizer.guests[i].guests[index].status) == 1) {
    				in_processGuests+= parseInt(ta_organizer.guests[i].guests[index].count)
    			}

    			if (parseInt(ta_organizer.guests[i].guests[index].status) == 2) {
    				doneGuests+= parseInt(ta_organizer.guests[i].guests[index].count)
    			}
    		}
    	}

    	this.newGuests = newGuests
    	this.in_processGuests = in_processGuests
    	this.doneGuests = doneGuests
  	},

  	pan (e) {
  		e.target.style.marginLeft = e.deltaX + 'px'
  	},

  	panend (e) {
  		e.target.style.marginLeft = '0px'
  	},

  	changeStatus (index, guestIndex) {
  		var currStatus = parseInt(ta_organizer.guests[index].guests[guestIndex].status)

  		if (currStatus >= 2) {
  			var newStatus = 0
  		} else {
  			var newStatus = currStatus + 1
  		}

  		ta_organizer.guests[index].guests[guestIndex].status = newStatus
  		localStorage.setItem('ta_organizer', JSON.stringify(ta_organizer))

  		this.recountStats()
  	},

  	deleteGuest (index, guestIndex) {
  		ta_organizer.guests[index].guests.splice(guestIndex, 1)
  		localStorage.setItem('ta_organizer', JSON.stringify(ta_organizer))

  		this.recountGuests()
  	},
  },
  created () {

  	console.log(this.organizer)

  	this.closeGroups()

  	this.recountStats()
  }
}
</script>

<style lang="sass" scoped>
#organizerGuests
	background: url('./assets/organizer-tasks-page-bg.jpg') center center no-repeat
	background-size: cover
	min-height: 100%
	padding-top: 40px
	padding-bottom: 150px

.taskStats
	text-align: center
	padding: 25px
	font-size: 18px
	color: #212121

	span
		color: #fd5a68

.taskStatsGraphs
	margin: 25px 15px 25px 38px

	.graph
		position: relative
		height: 45px
		line-height: 45px
		border-radius: 0 45px 45px 0
		margin: 15px 0

		div
			position: absolute
			top: 0
			left: 0
			height: 100%
			text-align: center
			overflow: hidden
			border-radius: 0 45px 45px 0
			color: #fff
			padding-left: 14px
			font-size: 12px

	.graph:before
		content: ''
		position: absolute
		z-index: 9
		top: 0
		left: -23px
		width: 45px
		height: 45px
		border-radius: 45px

	.graph.newTasks
		background: #d7dced

		div
			background: #4f5873

	.graph.newTasks:before
		background: #191d29 url('./assets/status-new-icon.png') center center no-repeat

	.graph.in_processTasks
		background: #f3ecd5

		div
			background: #e3c767

	.graph.in_processTasks:before
		background: #ffb400 url('./assets/status-process-icon.png') center center no-repeat

	.graph.doneTasks
		background: #e2f3f0

		div
			background: #7ecdc0

	.graph.doneTasks:before
		background: #2faf9a url('./assets/status-done-icon.png') center center no-repeat


.footer-block
  position: fixed
  bottom: 0
  left: 0
  width: 100%
  height: 150px

  p
    line-height: 40px
    text-align: center
    background: #fff
    box-shadow: 0 0 16px rgba(47, 175, 154, 0.5)
    font-size: 16px

  .groups-line
    overflow: scroll

    .task-gorups
      display: table
      table-layout: fixed

      .task-group
        display: table-cell
        width: 110px
        height: 110px
        vertical-align: middle
        text-align: center

.add-group
	margin: 15px
	position: relative

	input
		width: 100%
		padding: 10px 50px 10px 10px
		height: 40px
		font-size: 16px
		border: none
		border-bottom: 1px #f5f5f5 solid

	button
		position: absolute
		top: 40px
		right: 0
		width: 40px
		height: 40px
		box-shadow: 0 0 15px #ccc
		color: #fd5a68
		background: #fff
		font-size: 18px
		border-radius: 10px

.panstatus-new,
.panstatus-process,
.panstatus-done
	display: none
	background: #fd5a68
	position: absolute
	top: 0
	left: 0
	width: 100%
	height: 100%
	padding: 0 20px

.panstatus-process
	background: #fff59b

.panstatus-done
	background: #3fbda8


.task-item
	height: 45px
	line-height: 45px
	box-shadow: 0 0 15px rgba(0,0,0,0.5)
	margin: 15px -15px
	font-size: 18px
	color: #989898
	overflow: hidden

	.panner
		width: 100%
		-webkit-transition: 0.3s marginLeft ease
		-o-transition: 0.3s marginLeft ease
		transition: 0.3s marginLeft ease
		position: relative
		padding: 0 20px

		.panStatuses
			position: absolute
			top: 0
			right: 100%
			width: 100%
			height: 100%
			text-align: right
			color: #fff

		.pandelete
			position: absolute
			top: 0
			left: 100%
			width: 100%
			height: 100%
			color: #fff
			background: #fd5a68
			padding: 0 20px

	.panstatus-0
		.panstatus-process
			display: block

	.panstatus-1,
	.panstatus-2
		position: relative
		padding-left: 65px

	.panstatus-1:before
		content: ''
		position: absolute
		top: 0
		left: 0
		width: 45px
		height: 45px
		background: #fff59b url('./assets/status-process-icon.png') center center no-repeat
		border-radius: 0 50% 50% 0

	.panstatus-2:before
		content: ''
		position: absolute
		top: 0
		left: 0
		width: 45px
		height: 45px
		background: #3fbda8 url('./assets/status-done-icon.png') center center no-repeat
		border-radius: 0 50% 50% 0

	.panstatus-1
		.panstatus-done
			display: block

	.panstatus-2
		.panstatus-new
			display: block

.popup-footer
	text-align: center

.deleteItem:after
	content: ''
	display: inline-block
	width: 30px
	height: 30px
	background: url('./assets/delete-btn.png') center center no-repeat

.addItem:after
	content: ''
	display: inline-block
	width: 55px
	height: 55px
	background: url('./assets/add-item-btn.png') center center no-repeat
	margin-top: -10px

.closeItem:after
	content: ''
	display: inline-block
	width: 30px
	height: 30px
	background: url('./assets/exit-btn.png') center center no-repeat

</style>



