<template>
  <div id="organizer">
    <div class="page-header">
      	<a href="javascript:history.back();" v-if="goBack" class="back-link"></a>
		<div>{{ title }}</div>
	</div>

	<div class="organizer-menu">
		<router-link :to="menuItem.href" v-for="menuItem in menuItems" :class="'om-item ' + menuItem.alias">
			{{ menuItem.name }}
			<span>{{ menuItem.counter }}</span>
		</router-link>
	</div>

	<div class="organizer-footer">
		<div v-if="organizer.weddingDate" class="date-set">
			<p>{{ till_celebrating }}</p>
			<p>{{ organizer.weddingDate }}</p>
			<p>{{ left }}:</p>
			<div class="date-counter" v-html="timeLeft"></div>
		</div>
		<div v-else class="date-not-set">
			<p>{{ wedding_date_not_specified }}</p>
			<button @click="showDatePopup=true">{{ specify }}</button>
		</div>
	</div>

	<div class="popup" v-if="showDatePopup">
		<div class="popup-content">
			<p>{{ select_wedding_date }}</p>
			<div class="form-line">
				<flat-pickr v-model="organizer.weddingDate" :config="options" @on-close="setWeddingDate" class="form-element"></flat-pickr>
			</div>
		</div>
	</div>

  </div>
</template>

<script>
import flatPickr from 'vue-flatpickr-component'

export default {
  name: 'organizer',
  components: {
	  flatPickr
  },
  methods: {
  	setWeddingDate () {
  		ta_organizer = this.organizer
  		localStorage.setItem('ta_organizer', JSON.stringify(ta_organizer))

  		this.showDatePopup = false
  		this.setTimer()
  	},

  	setTimer() {
  	  if (!this.organizer.weddingDate) return

      var targetDate = new Date(this.organizer.weddingDate)/1000
      var currDate = Math.round(new Date().getTime()/1000)

      if (!targetDate || currDate > targetDate) return;

      var component = this

      var diff = targetDate - currDate;
      var days = Math.floor(diff / 86400);

      var hours_timestamp = diff - days*86400;
      var hours = Math.floor(hours_timestamp / 3600);

      var minutes_timestamp = hours_timestamp - hours*3600;
      var minutes = Math.floor(minutes_timestamp / 60);

      var seconds = minutes_timestamp - minutes*60;

      component.timeLeft = days + '<br>' + pad(hours, 2) + ':' + pad(minutes, 2) + ':' + pad(seconds, 2)

      setTimeout(function(){
          component.setTimer()
      }, 1000)
    },
  },
  data () {
    return {
    	title : '',

      	goBack : goBack(),

    	// main data storage of the component
    	organizer : ta_organizer,

      	menuItems : [
      		{
      			name : '',
      			href : '/organizerTasks',
      			counter : ta_organizer.tasksTotal | 0,
      			alias : 'tasks',
      		},
      		{
      			name : '',
      			href : '/organizerGuests',
      			counter : ta_organizer.guestsTotal | 0,
      			alias : 'guests',
      		},
      		{
      			name : '',
      			href : '/organizerCalculator',
      			counter : (ta_organizer.calcTotal | 0) + ' ',
      			alias : 'calc',
      		},
      	],

    	timeLeft : '0<br>00:00:00',

    	options : {
    		minDate : new Date(),
    	},

    	wedding_date_not_specified : '   ',
    	specify : '',
    	select_wedding_date : '   ',
    	till_celebrating : ' ',
    	left : '',

    	// popup settings
    	showDatePopup : false,

    }
  },

  created () {
  	this.setTimer()
  }
}
</script>

<style lang="sass" scoped>
#organizer
	background: url('./assets/organizer-page-bg.jpg') center center no-repeat
	background-size: cover
	min-height: 100%
	padding-bottom: 230px

.organizer-menu
	padding: 50px 25px 0

	.om-item
		display: block
		margin: 15px 0
		padding: 20px 60px 20px 20px
		background: rgba(255,255,255,0.75)
		font-family: Romul
		font-size: 20px
		line-height: 46px
		color: #212121
		text-transform: uppercase
		position: relative
		border-radius: 5px

		span
			float: right
			font-family: Romul
			font-size: 20px
			line-height: 46px
			color: #fd5a68

	.om-item:after
		content: ''
		position: absolute
		top: 35px
		right: 20px
		display: block
		width: 15px
		height: 15px
		background: url('./assets/task-arrow.png') center center no-repeat

	.om-item.calc
		line-height: 23px

		span
			float: none
			display: block
			margin-bottom: -15px

.organizer-footer
	position: fixed
	left: 0
	bottom: 0
	width: 100%
	height: 230px
	background: url('./assets/weddin-date-bg.png') center center no-repeat
	background-size: cover

	.date-not-set
		text-align: center
		color: #fff
		padding-top: 60px

		button
			margin-top: 55px
			width: 140px
			height: 44px
			color: #fff
			background: #FD5A60

	.date-set
		text-align: center
		color: #fff
		padding-top: 40px

		.date-counter
			width: 150px
			height: 80px
			margin: 20px auto
			padding: 10px
			background: #ddcdc3 url('./assets/counter-bg.jpg') center center no-repeat
			background-size: cover
			font-size: 20px
			color: #212121
			font-family: Romul
			line-height: 30px
			border-radius: 10px
			border: 1px #fff solid

.popup-content
	margin-top: 250px
</style>



