<template>
  <div id="about">
    <div class="page-header has-action">
      <a href="javascript:history.back();" v-if="goBack" class="back-link"></a>
      <router-link to="submit" class="action-link"></router-link>
      <div> </div>
    </div>
    <div class="bout-intro" v-html="discription"></div>
    <div class="about-buttons">
    	<div class="row">
    		<div class="half">
				<a href="https://facebook.com" class="button-link">
					<img src="./assets/fb-icon.png">
				</a>
    		</div>
    		<div class="half">
    			<a href="https://facebook.com" class="button-link">
					<img src="./assets/instagram-icon.png">
				</a>
			</div>
    	</div>
    	<div class="row">
    		<div class="full">
    			<a href="https://play.google.com" class="button-link feedback-link">
					 
				</a>
    		</div>
    	</div>
    	<div class="row">
    		<div class="half">
				<a href="mailto:info@prosite5.com" class="button-link">
					 
				</a>
    		</div>
    		<div class="half">
    			<router-link to="submit" class="button-link">
					 
				</router-link>
			</div>
    	</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'about',
  data () {
    return {
      discription : ' -     .<br> -  ',

      goBack : goBack(),
    }
  }
}
</script>

<style lang="sass">
#about
	padding-top: 40px
	min-height: 100%
	background: #4cc0ae url('./assets/about-page-bg.jpg') center 40px no-repeat
	background-size: 100% auto


.bout-intro 
	max-width: 200px
	margin: 15px
	padding: 10px 15px 
	background: rgba(255,255,255,0.75)
	border-radius: 5px

.about-buttons
	margin-top: 100px

.row
	padding: 7px 15px 8px
	clear: both

	.half
		float: left
		width: 50%
		padding: 0 8px

	.full 
		margin: 0 8px
	
.row:after
	content: ''
	display: block
	clear: both

.button-link 
	display: block
	width: 100%
	height: 75px
	background: #fff
	color: #212121
	font-size: 18px
	padding: 15px
	text-align: center
	border-radius: 5px

.feedback-link
	position: relative
	padding-top: 25px

.feedback-link:after
	content: ''
	display: block
	width: 43px
	height: 28px
	position: absolute
	right: 20px
	top: 50%
	margin-top: -14px
	background: url('./assets/feedback-link.png') no-repeat


</style>



