<template>
  <div id="menu">
    <v-touch v-on:swipeleft="menuShow = false">
    <transition name="slideFromLeft">
      <aside id="aside" v-show="menuShow">
        <div class="aside-content">
          <div  id="city" @click="menuShow=false">
            <router-link to="/cities">{{ city }}</router-link>
          </div>
          <div id="logo"><img src="assets/logo.png"></div>
          <ul id="menu-items" @click="menuShow=false">
            <li v-for="menuItem in menuItems">
              <router-link :to="'/' + menuItem.className" v-bind:class="menuItem.className">
                {{ menuItem.name }}
                <span v-html="menuItem.count"></span>
              </router-link>
            </li>
          </ul>
          <div class="aside-footer">
          </div>
        </div>
      </aside>
    </transition>
    </v-touch>

    <v-touch v-on:swiperight="menuShow = true">
        <div id="menu-picker"></div>
    </v-touch>
  </div>
</template>

<script>
import Vue from 'vue'
import { EventBus } from '../event-bus.js';
var VueTouch = require('vue-touch')
Vue.use(VueTouch, {name: 'v-touch'})

export default {
  name: 'menu',
  data () {
    return {
      menuShow : true,
      city : ta_city,
      user_id : ta_user_id,
      menuItems : [
        {
          name: '',
          count: '',
          className: ''
        },
        {
          name: '',
          count: (localStorage.getItem('ta_favorites_count')) ? localStorage.getItem('ta_favorites_count') : 0,
          className: 'favorites'
        },
        {
          name: '',
          count: '',
          className: 'organizer'
        },
        {
          name: '',
          count: '',
          className: 'tender'
        },
        {
          name: ' ',
          count: '',
          className: 'about'
        }
      ]
    }
  },
  methods : {
    updateCounters : function () {
      // set loading effect
      this.menuItems[0].count = '<img src="assets/loading.gif" width="12">';
      this.menuItems[3].count = '<img src="assets/loading.gif" width="12">';

      // make the request to find out the count of counted menu items
      this.$http.get(api_host + 'api/menu/counters').then(function (data) {
          this.menuItems[0].count = data.body.catalog;
          this.menuItems[3].count = data.body.tender;
      })
    }
  },
  watch : {
    // '$route' () {
    //   this.menuShow = false
    // }
  },
  created : function () {
    this.updateCounters();

    // handle emited events from other components
    var current = this;
    // city changed
    EventBus.$on('city-selected', function(city) {
      current.city = city.name
    })
    // favorites toggled
    EventBus.$on('favorite-toggled', function(favorites) {
      current.menuItems[1].count = favorites.count
    })
    // user logged
    EventBus.$on('user-logged', function(user_id) {
      current.user_id = user_id
    })
    // user exited
    EventBus.$on('user-exited', function() {
      current.user_id = 0
    })
  }
}
</script>

<style lang="sass">

aside,
#menu-picker
  position: fixed
  z-index: 9998
  left: 0
  top: 0
  width: 100%
  height: 100%

.aside-content
  position: absolute
  top: 0
  left: 0
  width: 70%
  height: 100%
  background: #40bda9
  color: #fff
  overflow-x: hidden

.aside-content:before
  content: ''
  width: 110%
  height: 150px
  border-radius: 100%
  position: absolute
  top: -75px
  left: -5%
  background: #3cb19f
  box-shadow: 0 40px 0 100px #3eb7a4
  z-index: 0

.aside-content .aside-footer
  position: absolute
  bottom: 0
  left: 0
  height: 90px
  width: 100%
  overflow: hidden

.aside-footer:after
  content: ''
  width: 100%
  height: 150px
  border-radius: 100%
  position: absolute
  bottom: -60px
  left: 0
  background: #3cb19f
  /*box-shadow: 0 40px 0 100px #3eb7a4*/
  z-index: 0

.aside-content *
  position: relative
  z-index: 1

#menu-picker
  content: ''
  position: fixed
  top: 0
  left: 0
  width: 10px
  height: 100%

#city
  position: absolute
  z-index: 2
  top: 20px
  left: 0
  width: 100%
  height: 30px
  line-height: 30px
  text-align: center

  a
    font-size: 14px
    color: #fff

  a:after
    content: ''
    display: inline-block
    width: 14px
    height: 14px
    background: url('./assets/city-arr-down.png') center center no-repeat
    position: relative
    top: 3px
    left: 10px

#logo
  text-align: center
  padding-top: 110px

#menu-items
  margin: 75px 10%

  li
    a
      padding: 0 15px 0 0
      border-bottom: 1px #5cc7b6 solid
      color: #fff
      line-height: 55px
      font-size: 16px
      font-family: Romul
      text-transform: uppercase
      font-weight: normal

    a:before
      content: ''
      float: left
      margin: 10px 10px 0 0
      width: 29px
      height: 29px
      background: url(./assets/menuPattern.png) left top no-repeat
      background-size: auto 31px

    a.favorites:before
      background-position: -26px 0

    a.organizer:before
      background-position: -52px 0

    a.tender:before
      background-position: -80px 0

    a.about:before
      background-position: -106px 0


#menu-items
  li
    span
      float: right
      font-size: 12px

</style>



