<template>
  <div id="category">
    <div class="page-header">
      <a href="javascript:history.back();" v-if="goBack" class="back-link"></a>
      <div>{{ category.name }}</div>
    </div>
    <div v-for="item in items" class="catalog-item">
      <router-link :to="'/item/' + item.id">
        <div class="item-heading">
          <div class="item-category">{{ item.category }}</div>
          <div class="item-rating">
            <img v-for="i in item.rating" src="./assets/rating-star.png">
          </div>
        </div>
        <div class="item-images">
          <div class="img-main-1">
            <img :src="item.mainImage" v-on:load="placeMe($event)">
          </div>
          <div v-if="item.images.length > 0" v-for="image in item.images" class="img-main-0'" :style="'background-image:url('+image.src+')'"></div>
        </div>
        <div class="item-footer" v-html="item.name"></div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'category',
  data () {
    return {
      category : {},
      items : [],

      goBack : goBack(),
    }
  },
  methods : {
  	getCategory : function() {
      this.$http.get(api_host + 'api/category/' + this.$route.params.id, {
        params : { 'city_id' : window.ta_city_id }
      })
        .then(function (data) {
          this.category = data.body.category;
          
          for (var i in data.body.items) {
            this.items.push(data.body.items[i]);
          }
        })
    },

    placeMe : function(event) {
      var el = event.target
      var elWidth = el.offsetWidth
      var elHeight = el.offsetHeight

      var container = el.parentNode
      var containerWidth = container.offsetWidth
      var containerHeight = container.offsetHeight

      var ratio = elWidth / elHeight

      var newHeight = containerHeight
      var newWidth = newHeight * ratio

      var marginTop = 0
      var marginLeft = (containerWidth - newWidth) / 2

      if (newWidth < containerWidth) {
        var newWidth = containerWidth
        var newHeight = newWidth / ratio

        var marginTop = (containerHeight - newHeight) / 2
        var marginLeft = 0
      }

      el.style.width = newWidth + 'px'
      el.style.height = newHeight + 'px'
      el.style.marginTop = marginTop + 'px'
      el.style.marginLeft = marginLeft + 'px'
    }
  },
  watch : {
    '$route' (to, from) {
      this.getCategory();
    }
  },
  created : function () {
    this.getCategory();
  }
}
</script>

<style lang="sass">
#category
  padding-top: 40px

.catalog-item 
  width: 100%
  height: 300px
  position: relative

  .item-heading 
    position: absolute
    z-index: 99
    top: 0
    kleft: 0
    width: 100%
    height: 30px
    line-height: 30px
    background: rgba(0,0,0,0.5);
    padding: 0 15px

    .item-category
      float: left
      font-size: 14px
      color: #fff

    .item-rating 
      float: right

  .item-images

    div
      position: absolute
      z-index: 1
      left: 0
      top: 0
      width: 100%
      height: 100%
      background-repeat: no-repeat
      background-position: center center
      background-color: #fff
      background-size: cover
      overflow: hidden

    .img-main-1
      z-index: 2

  
  .item-footer 
    position: absolute;
    z-index: 99
    bottom: 0
    left: 0
    margin: 15px 3%
    width: 94%
    min-height: 30px
    padding: 5px 10px
    line-height: 20px
    text-align: center
    background: url('./assets/item-name-bg.png')
    background-size: 100% 100%
    color: #fff
    font-size: 16px
</style>



