<template>
  <div id="home">
  	<swiper :options="swiperOption">
		<swiper-slide v-for="slide in slides">
			<router-link :to="slide.link">
		    	<img :src="slide.src" class="slide-image">
			</router-link>
		</swiper-slide>
	</swiper>
	
  	<ul id="categories">
  		<li v-for="category in categories">
		    <router-link :to="'/category/' + category.id" class="category">
		    	<img :src="category.img">
		    	<span>{{ category.name }}</span>
			</router-link>
  		</li>
  	</ul>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
    	categories : [],
    	slides : [],
    	swiperOption : {
		  	slidesPerView: 1,
          	lspaceBetween: 30,
		    loop: true,
		    autoplay: 3000,
		    autoplayDisableOnInteraction: false,
          	initialSlide: 1,
    	}
    }
  },
  methods : {
  	getCategories : function() {
		this.$http.get(api_host + 'api/categories').then(function (data) {

			console.log(data.body)

			for (var i in data.body.items) {
				this.categories.push(data.body.items[i]);
			}


			for (var s in data.body.slides) {
				this.slides.push(data.body.slides[s]);
			}

			console.log(data.body.slides);

		})
  	}
  },
  created : function () {
  	this.getCategories();
  }
}
</script>

<style lang="sass">
#categories

	li
		.category
			float: left
			width: 33.33%
			height: 100px
			text-align: center
			color: #ccc
			padding: 10px
			overflow: hidden
			border: 1px #f5f5f5 solid
			border-top: none
			border-left: none

			img
				height: 40px
				margin-bottom: 5px

			span
				display: block
				font-size: 14px
				color: #7a7a7a
		
	li:nth-child(3n)

		.category
			border-right: none

.slide-image
	width: 100%

.swiper-container 
	width: 100%
	overflow: hidden
	
	*
		-webkit-tap-highlight-color: transparent

.swiper-wrapper
	width: 4000000px

.swiper-slide 
	float: left

</style>



