import Vue from 'vue'

// install dependencies
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import VueResource from 'vue-resource'
Vue.use(VueResource)

import VueAwesomeSwiper from 'vue-awesome-swiper'
Vue.use(VueAwesomeSwiper)


// import components
import App from './App.vue'
import Home from './components/Home.vue'
import Category from './components/Category.vue'
import Item from './components/Item.vue'
import Favorites from './components/Favorites.vue'
import Organizer from './components/Organizer.vue'
import OrganizerTasks from './components/OrganizerTasks.vue'
import OrganizerGuests from './components/OrganizerGuests.vue'
import OrganizerCalculator from './components/OrganizerCalculator.vue'
import Tender from './components/Tender.vue'
import About from './components/About.vue'
import Cities from './components/Cities.vue'
import Login from './components/Login.vue'
import Register from './components/Register.vue'
import Profile from './components/Profile.vue'

const routes = [
    { path: '/', component: Home },
    { path: '/category/:id', component: Category },
    { path: '/item/:id', component: Item },
    { path: '/favorites', component: Favorites },
    { path: '/organizer', component: Organizer },
    { path: '/organizerTasks', component: OrganizerTasks },
    { path: '/organizerGuests', component: OrganizerGuests },
    { path: '/organizerCalculator', component: OrganizerCalculator },
    { path: '/tender', component: Tender },
    { path: '/about', component: About },
    { path: '/cities', component: Cities },
    { path: '/login', component: Login },
    { path: '/register', component: Register },
    { path: '/profile', component: Profile },
]

const router = new VueRouter({
    routes
    // mode: 'history'
});

// main app settings
window.ta_user_id   = (localStorage.getItem('ta_user_id')) ? localStorage.getItem('ta_user_id') : '0';
window.ta_lang      = (localStorage.getItem('ta_lang')) ? localStorage.getItem('ta_lang') : 'ua';
window.ta_city_id   = (localStorage.getItem('ta_city_id')) ? localStorage.getItem('ta_city_id') : 0;
window.ta_city      = (localStorage.getItem('ta_city')) ? localStorage.getItem('ta_city') : ' ';
window.api_host     = 'http://kupidon.if.ua/';

window.ta_organizer = (localStorage.getItem('ta_organizer')) ? JSON.parse(localStorage.getItem('ta_organizer')) : {};


// global functions
window.goBack = function() {
    return history.length;
};

window.pad = function (num, size) {
    var s = num + "";
    while (s.length < size) s = "0" + s;

    return s;
}


var app = {

    initialize: function() {
        this.bindEvents();
    },


    bindEvents: function() {
        document.addEventListener('deviceready', this.onDeviceReady, false);
    },


    onDeviceReady: function() {
        app.setupPush();

        document.getElementById('loadingApp').classList.add('fadeOut');
        document.getElementById('vue_app').classList.remove('fadeOut');

        new Vue({

          el: '#vue_app',
          router,
          components: {
          	App
          }

        });
    },


    // handles the push notifications
    setupPush: function() {
        var push = PushNotification.init({
            "android": {
                "senderID": "242553844219"
            },
            "browser": {},
            "ios": {
                "alert": false,
                "sound": true,
                "vibration": true,
                "badge": true
            }
        });

        // fires, when the device registers and get the registration ID
        push.on('registration', function(data) {
            var oldRegId = localStorage.getItem('registrationId');
            if (oldRegId !== data.registrationId) {
                localStorage.setItem('registrationId', data.registrationId);
            }
        });

        // fires, when th notification fails
        push.on('error', function(e) {
        });

        // fires, when the notification received
        push.on('notification', function(data) {
       });
    }
};


app.initialize();



