<template>
  <div id="item">
    <div class="page-header">
      <a href="javascript:history.back();" v-if="goBack" class="back-link"></a>
      <div>{{ item.name }}</div>
    </div>
    <div class="catalog-item">
      <div class="item-heading">
          <div class="item-category">{{ item.catName }}</div>
          <div class="item-rating">
            <img v-for="i in item.rating" src="./assets/rating-star.png">
          </div>
        </div>
        <div class="item-images">
          <div class="img-main-1">
            <img :src="item.mainImage" v-on:load="placeMe($event)">
          </div>
        </div>
        <div class="item-footer">
          {{ item.name }}
        </div>
    </div>
    <div class="to-favorites" :class="{ 'is-favorite' : is_favorite }" @click="toggleFavorites()">
      <span v-if="is_favorite">  </span>
      <span v-else>  </span>
    </div>
    <div class="item-description" v-html="item.description"></div>
    <div class="item-contacts">
      <div v-if="item.phone.length" class="ic-phone" v-html="item.phone"></div>
      <div v-if="item.email.length" class="ic-email" v-html="item.email"></div>
      <div v-if="item.site.length" class="ic-site" v-html="item.site"></div>
    </div>
    <div class="tabs">
      <div :class="(tabindex==1) ? 'active' : ''" @click="tabindex=1"></div>
      <div :class="(tabindex==2) ? 'active' : ''" @click="tabindex=2"></div>
    </div>
    <div class="tab-content">
      <div v-show="(tabindex==1)" class="clr">
        <div class="row">
          <div class="col col-33 add-image" v-for="image in item.images">
            <img :src="image.src">
          </div>
        </div>
      </div>
      <div v-show="(tabindex==2)">
        <div v-if="item.reviews.length">
          <div class="review-item" v-for="review in item.reviews">
            <img :src="review.avatar" class="review-avatar">
            <div class="review-body">
              <div :class="'review-rationg rating-' + review.rating"></div>
              <span class="review-author">{{ review.name }} {{ review.name2 }}</span>
              <p>{{ review.comment }}</p>
              <span class="review-date">{{ review.date }}</span>
            </div> 
          </div>
        </div>
        <div v-else class="no-reviews">  .</div>
      </div>
    </div>
</template>

<script>
import { EventBus } from '../event-bus.js';

export default {
  name: 'item',
  data () {
    return {
      item : {},
      tabindex : 1,
      is_favorite : false,

      goBack : goBack(),
    }
  },
  methods : {
  	getItem : function() {
      this.$http.get(api_host + 'api/item/' + this.$route.params.id).then(function (data) {
        this.item = data.body.item;
        this.checkFavorite();
      })
    },

    checkFavorite : function() {
      var favorites = localStorage.getItem('ta_favorites')
      var favorites_arr = (favorites) ? favorites.split(',') : [];
      console.log(favorites_arr)

      this.is_favorite = (favorites_arr.indexOf(this.item.id) >= 0) ? true : false
    },

    toggleFavorites : function()
    {
      this.is_favorite = !this.is_favorite;

      var favorites = localStorage.getItem('ta_favorites')
      var favorites_arr = (favorites) ? favorites.split(',') : []

      var item = this.item

      if (this.is_favorite) {
        favorites_arr.push(this.item.id)
      } else {
        favorites_arr = favorites_arr.filter(function (el) {
            return el != item.id
        })
      }

      localStorage.setItem('ta_favorites_count', favorites_arr.length);
      EventBus.$emit('favorite-toggled', { 'count' : favorites_arr.length} );

      var new_favorites = favorites_arr.join(',')
      localStorage.setItem('ta_favorites', new_favorites)
    },    

    placeMe : function(event) {
      var el = event.target
      var elWidth = el.offsetWidth
      var elHeight = el.offsetHeight

      var container = el.parentNode
      var containerWidth = container.offsetWidth
      var containerHeight = container.offsetHeight

      var ratio = elWidth / elHeight

      var newHeight = containerHeight
      var newWidth = newHeight * ratio

      var marginTop = 0
      var marginLeft = (containerWidth - newWidth) / 2

      if (newWidth < containerWidth) {
        var newWidth = containerWidth
        var newHeight = newWidth / ratio

        var marginTop = (containerHeight - newHeight) / 2
        var marginLeft = 0
      }

      el.style.width = newWidth + 'px'
      el.style.height = newHeight + 'px'
      el.style.marginTop = marginTop + 'px'
      el.style.marginLeft = marginLeft + 'px'
    }
  },
  watch : {
    '$route' (to, from) {
      this.getItem();
    }
  },
  created : function () {
    this.getItem();
  }
}
</script>

<style lang="sass">
#item
  padding-top: 40px

.catalog-item 
  width: 100%
  height: 300px
  position: relative

  .item-heading 
    position: absolute
    z-index: 99
    top: 0
    kleft: 0
    width: 100%
    height: 30px
    line-height: 30px
    background: rgba(0,0,0,0.5);
    padding: 0 15px

    .item-category
      float: left
      font-size: 14px
      color: #fff

    .item-rating 
      float: right

  .item-images

    div
      position: absolute
      z-index: 1
      left: 0
      top: 0
      width: 100%
      height: 100%
      background-repeat: no-repeat
      background-position: center center
      background-color: #fff
      background-size: cover

    .img-main-1
      z-index: 2
      height: 300px
      overflow: hidden

  
  .item-footer 
    position: absolute;
    z-index: 99
    bottom: 0
    left: 0
    margin: 15px 3%
    width: 94%
    min-height: 30px
    padding: 5px 10px
    line-height: 20px
    text-align: center
    background: url('./assets/item-name-bg.png')
    background-size: 100% 100%
    color: #fff
    font-size: 16px

.item-description 
  padding: 15px
  font-size: 14px
  color: #212121
  line-height: 1.5

  ul, ol
    padding-left: 25px
    margin: 5px 0

    li 
      list-style-type: disc

.tabs
  height: 30px
  border-bottom: 1px #40bda9 solid
  
  div
    float: left
    width: 50%
    height: 30px
    line-height: 30px
    text-align: center
    font-size: 14px
    text-transform: uppercase
    color: #fd5a68

  div.active
    color: #fff
    background: #40bda9

.tab-content
  
  > div
    padding: 0 10px 10px

.add-image 
  overflow: hidden
  height: 75px

  img
    width: 100%

.review-item 
  margin: 10px 0
  font-size: 14px
  color: #212121

.review-avatar
  float: left
  width: 40px
  border-radius: 40px
  margin: 0 10px 0 0

.review-body
  margin-left: 50px

.review-rationg
  float: right
  width: 81px
  height: 13px
  background: url('./assets/review-rating.png') left top no-repeat
  position: relative

.review-rationg:before
  content: ''
  position: absolute
  top: 0
  left: 0
  height: 100%
  width: 0
  background: url('./assets/review-rating.png') left bottom no-repeat

.review-rationg.rating-1:before
  width: 20%

.review-rationg.rating-2:before
  width: 40%

.review-rationg.rating-3:before
  width: 60%

.review-rationg.rating-4:before
  width: 80%

.review-rationg.rating-5:before
  width: 100%

.review-author
  display: block
  margin-bottom: 10px

.review-date 
  display: block
  margin-top: 10px
  font-size: 12px
  color: #989898

.no-reviews 
  margin: 20px 0
  font-size: 14px
  color: #989898
  text-align: center

.item-contacts
  width: 80%
  margin: 0 auto 30px

  div 
    background: #fbfbfb
    border: 1px #e9e9e9 solid
    border-radius: 15px
    padding: 10px 10px 10px 50px
    line-height: 20px
    margin: 10px 0
    position: relative

    a, span
      font-size: 14px
      color: #212121

  div:before
    content: ''
    position: absolute
    top: 50%
    left: 10px
    width: 30px
    height: 20px
    margin-top: -10px
    border-right: 1px #e9e9e9 solid
    background: url('./assets/item-contacts.png') left top no-repeat;

  .ic-email:before
    background-position: left -20px

  .ic-site:before
    background-position: left -40px

.to-favorites
  width: 80%
  margin: 25px auto 0
  padding: 0 10px
  line-height: 32px
  text-align: center
  border: 1px #e9e9e9 solid
  border-radius: 15px

.to-favorites:before
  content: ''
  display: inline-block
  width: 24px
  height: 24px
  background: url('./assets/favorites-sprite.png') left top no-repeat
  background-size: auto 100%
  position: relative
  top: 5px
  right: 15px

.to-favorites.is-favorite
  background: #fd5a68
  color: #fff

.to-favorites.is-favorite:before
  background-position: right top

</style>



