<template>
  <div id="login">
    <div class="page-title">{{ pageTitle }}</div>
    <div class="login-form">
    	<form action="#" method="post" autocomplete="off" @submit="auth" id="login-form">
    		<div id="login-message" v-if="message.length > 0" v-html="message"></div>
    		<div class="form-line">
    			<input type="text" id="auth_login" name="login" :placeholder="email">
    		</div>
    		<div class="form-line">
    			<input type="password" id="auth_password" name="password" :placeholder="password">
    		</div>
    		<div class="form-line">
    			<button type="submit">{{ log_in }}</button>
    		</div>
    		<div class="form-links">
    			<router-link to="/register">{{ sign_in }}</router-link>
    		</div>
    	</form>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { EventBus } from '../event-bus.js';

export default {
  name: 'login',
  data () {
    return {
      pageTitle : '',
      message : '',

      email: '.',
      password: '',
      log_in: '',
      sign_in: '',
    }
  },
  methods : {
  	auth : function (e) {
  	  e.preventDefault();

  	  this.message = '';

  	  var data = {
  	  	login : document.getElementById('auth_login').value,
  	  	password : document.getElementById('auth_password').value,
  	  }

  	  this.$http.post(api_host + 'api/user/auth', data, { emulateJSON: true, emulateHTTP: true}).then(function (data) {
        if (data.body.status_code==0) {
        	this.message = data.body.message;
        	return;
        }
        
  	  	this.message = '';

  	  	window.ta_user_id = data.body.user_id;
  	  	localStorage.setItem('ta_user_id', data.body.user_id);
  	  	EventBus.$emit('user-logged', data.body.user_id);

  	  	this.$router.push('/');
      })
  	}, 
  },
  created : function () {
  	if (window.ta_user_id > 0) {
  	  this.$router.push('/profile');
  	}
  }
}
</script>

<style lang="sass">
#login
	background: url('./assets/login-page-bg.jpg') center center no-repeat
	background-size: cover
	min-height: 100%

	form
		position: absolute
		top: 50%
		margin-top: -90px
		width: 100%

	.form-line
		height: 60px
		line-height: 60px
		width: 90%
		margin: 0 auto

		input
			width: 100%
			height: 40px
			border: 0
			outline: 0
			background: #fff
			padding: 5px 15px
			font-size: 16px
			border-bottom: 1px #fd5a68 solid

		button
			width: 100%
			height: 40px
			background: #fd5a68
			color: #fff
			text-transform: uppercase
			border: 0
			outline: 0
			font-size: 14px

	.form-links
		text-align: center
		line-height: 60px

		a
			color: #fff


.page-title 
	position: absolute
	top: 50px
	left: 50%
	width: 150px
	height: 150px
	line-height: 150px
	margin-left: -75px
	text-align: center
	font-size: 25px
	color: #fff
	text-transform: uppercase
	font-family: 'Romul'
	background: url('./assets/login-page-title.png') center center no-repeat

#login-message
	text-align: center
	color: #fff
	padding: 20px 0
	position: absolute
	top: -50px
	left: 0
	width: 100%

</style>



