<template>
  <div id="app" v-bind:class="{'showing':menuShow}">
    <Menu></Menu>
    <router-view></router-view>
  </div>
</template>

<script>
import Menu from './components/Menu.vue'

export default {
  name: 'app',
  components: {
    Menu
  },
  methods : {
    LoadOrganiser () {
      this.$http.get(api_host + 'api/organizer/start').then(function (data) {
        ta_organizer = data.body
        localStorage.setItem('ta_organizer', JSON.stringify(ta_organizer))
      })
    },
  },
  created () {
    if (!localStorage.getItem('ta_organizer')) {
      this.LoadOrganiser()
    }
  }
}
</script>

<style lang="sass">
@font-face
  font-family: "Avanti"
  src: url('fonts/Avanti/Avanti.ttf')

@font-face
  font-family: "Romul"
  src: url('fonts/Romul/romul.ttf')

*
  box-sizing: border-box
  margin: 0
  padding: 0
  outline: 0
  list-style: none
  font-family: Avanti
  box-sizing: border-box

html, body,
  height: 100%

body
  -webkit-touch-callout: none
  -webkit-text-size-adjust: none
  -webkit-user-select: none
  touch-action: manipulation
  height: 100%
  width: 100%
  margin: 0px
  padding: 0px

.app-container,
#vue_app,
#app
  height: 100%


a
  text-decoration: none

h1
  text-decoration: underline

.pull-right
  float: right

.row
  margin: 0 -5px

.col
  float: left

.col-33
  width: 33.33%
  padding: 10px 5px

.clr:before,
.clr:after
  content: ''
  display: block
  clear: both

.slideFromLeft-enter-active, .slideFromLeft-leave-active
  transition: left .2s

.slideFromLeft-enter, .slideFromLeft-leave-to
  left: -100%

.page-header
  position: fixed
  left: 0
  top: 0
  z-index: 999
  background: #fff
  height: 40px
  line-height: 40px
  width: 100%
  padding: 0 20px
  color: #000

  div
    width: 85%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

  .back-link
    float: left
    height: 40px
    width: 40px
    background: url('./assets/back-arrow.png') left center no-repeat

  .action-link
    float: right
    font-size: 12px
    color: #b3b3b3

.page-header.has-action

  div
    width: 65%

.list-empty
  margin: 15px
  text-align: center
  color: #999

.form-line
  margin: 10px 0

.form-element
  width: 100%
  padding: 5px 10px
  border: 1px #ccc solid
  font-size: 16px

.popup
  position: fixed
  z-index: 9999
  top: 0
  left: 0
  width: 100%
  height: 100%
  background: rgba(0,0,0,0.5)

  .popup-content
    position: relative
    margin: 25px
    border-radius: 15px
    box-shadow: 0 5px 8px rgba(0,0,0,0.5)
    background: #fff
    padding: 15px

    .close-popup
      position: absolute
      z-index: 99
      top: 15px
      right: 15px
      width: 20px
      height: 20px
      background: url('./assets/popup-exit.png') center center no-repeat

    .popup-title
      text-align: center
      font-size: 16px
      color: #212120
      padding: 16px
      text-transform: uppercase
      position: relative
      margin-bottom: 15px

    .popup-title:after
      content: ''
      position: absolute
      bottom: 0
      left: 50%
      width: 80%
      margin-left: -40%
      border-bottom: 1px #3fbda8 solid

  .popup-footer
    margin-top: 15px

  .popup-footer:after
    content: ''
    clear: both
    display: block

</style>



