<template>
  <div id="tender">
    <div class="page-header">
      <a href="javascript:history.back();" v-if="goBack" class="back-link"></a>
      <div>{{ title }}</div>
    </div>

    <div class="tender-description">
      {{ decription }}
    </div>

    <div class="tender-steps">
      <div>{{ select_service }}</div>
      <div>{{ enter_price }}</div>
      <div>{{ wait_contact }}</div>
    </div>

    <div class="create-tender">{{ add_tender }}</div>

    <div class="tender">
      <div class="tender-tabs">
        <div :class="(tabIndex==0) ? 'active' : ''" @click="tabIndex=0">
          <span>{{ tendersLbl }} ({{ tenders[0].length }})</span>
        </div>
        <div id="propositionTab" :class="(tabIndex==1) ? 'active' : ''" @click="tabIndex=1">
          <span>{{ suppliesLbl }} ({{ tenders[1].length }})</span>
        </div>
      </div>
      <div class="tender-blocks">
        <div v-if="tabIndex==0" class="tender-group">
          <div class="tender-item" v-for="tender in tenders[0]">
            <span>{{ tender.service }}</span>
            <span class="pull-right">{{ tender.budget }}</span>
          </div>
        </div>
        <div v-if="tabIndex==1" class="tender-group">
          <div class="tender-item" v-for="tender in tenders[1]">
            <span>{{ tender.service }}</span>
            <span class="pull-right">{{ tender.budget }}</span>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'tender',
  data () {
    return {
    	title : '',

      goBack : goBack(),

      decription : '    -     -        ',
      select_service : '  ',
      enter_price : '  ',
      wait_contact : ',   \'',

      add_tender : ' ',

      tendersLbl : '',
      suppliesLbl : '',

      tabIndex : 0,

      tenders : [
        [],
        [],
      ],
      currency : '',

    }
  },
  methods : {
    getItems () {
      this.$http.get(api_host + 'api/tender', {
        params : { 'city_id' : window.ta_city_id }
      })
      .then(function (data) {
        this.tenders = data.body
      })
    },
  },
  created () {
    this.getItems()
  }
}
</script>

<style lang="sass">
#tender
  padding-top: 40px
  min-height: 100%
  background: #fff url('./assets/tender-page-bg.jpg') center 40px no-repeat
  background-size: 100% auto

.tender-description
  padding: 30px 30px 30px 50%
  text-align: right

.tender-steps
  margin: 10px 10px 30px 10px
  height: 150px
  background: url('./assets/tender-arrows.png') center center no-repeat
  background-size: 100% 100%
  &::after
    content: ''
    display: block
    clear: both

  div
    float: left
    width: 33%
    height: 150px
    padding: 50px 35px
    font-size: 14px
    &:last-child
      padding-left: 15px

.create-tender
  background: url('./assets/item-name-bg.png') center center no-repeat
  background-size: 100% 100%
  height: 40px
  line-height: 40px
  margin: 30px 15px
  text-align: center
  color: #fff
  font-size: 18px

.tender
  .tender-tabs
    height: 40px
    overflow: hidden
    div
      float: left
      width: 50%
      position: relative
      line-height: 48px
      text-align: center
      font-size: 14px
      &::before
        content: ''
        position: absolute
        z-index: 0
        left: 0
        top: 0
        width: 110%
        height: 40px
        background: url('./assets/tender-tab.png') left top no-repeat
        background-size: 100% 100%

      span
        position: relative
        display: block
        width: 100%
        z-index: 1
        color: #7a7a7a

    div#propositionTab
      &::before
        right: -10px
        left: auto

    div.active
      &::before
        background: url('./assets/tab-active.png') left top no-repeat
        background-size: 100% 100%

      span
        color: #fff

.tender-group
  padding: 20px
  background: #fd5a68

  p
    color: #fff

  .tender-item
    background: #ffffff
    border-radius: 5px
    margin: 2px 0
    padding: 15px
    font-size: 14px
</style>



