<template>
  <div id="cities">
    <div class="page-header">
      <a href="javascript:history.back();" v-if="goBack" class="back-link"></a>
      <div>{{ page_header }}</div>
    </div>
    <ul>
      <li :class="(current_city==0) ? 'active' : ''">
        <span @click="setCity(0, ' ')"> </span>
      </li>
      <li v-for="city in cities[0]" :class="(current_city==city.id) ? 'active' : ''">
        <span @click="setCity(city.id, city.name)">{{ city.name }}</span>
        <ul v-if="cities[city.id]">
          <li v-for="subcity in cities[city.id]" :class="(current_city==subcity.id) ? 'active' : ''">
            <span @click="setCity(subcity.id, subcity.name)">{{ subcity.name }}</span>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
import Vue from 'vue'
import { EventBus } from '../event-bus.js';

export default {
  name: 'cities',
  data () {
    return {
      page_header : ' ',
      current_city : window.ta_city_id,
      cities : {},

      goBack : goBack(),
    }
  },
  methods : {
  	getItems : function() {
      this.$http.get(api_host + 'api/cities').then(function (data) {
        console.log(data);
        this.cities = data.body.cities;
      })
    },

    setCity : function(id, name)
    {
      // update current options
      window.ta_city_id = this.current_city = id;
      window.ta_city  = name;

      // update storage settings
      localStorage.setItem('ta_city_id', id);
      localStorage.setItem('ta_city', name);

      EventBus.$emit('city-selected', {'id': id, 'name': name});

      this.$router.go(-1);
    }
  },
  created : function () {
    this.getItems();
  }
}
</script>

<style lang="sass">
#cities 
  padding-top: 40px

  ul
    li
      span
        display: block
        line-height: 40px
        padding: 0 15px
        border-top: 1px #f2f2f2 solid

    li.active
      background: #f2f2f2
      
    ul
      li
        span:before
          content: " "
</style>



